.bd {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen' ,
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif ;
  -webkit-font-smoothing: antialiased  ;
  -moz-osx-font-smoothing: grayscale  ;
  background: linear-gradient(to bottom, #9d4edd, rgba(0, 0, 0, 0.943)) ;
}


code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace  !important;
}

.bd .btn {
  background-color: #ef93e4 !important;
  border: 2.5px solid #E207B9 !important;
  background-color: #277088de  !important;
  color: #E207B9 !important;
  font-weight: bold  !important;
  box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.5)  !important;
  transition: transform 0.2s ease-in-out  !important;
}
.bd .btn.blue {
  background-color: #A4BCFF !important;
  border: 2.5px solid #134aef !important;
  background-color: #277088de  !important;
  color: #E207B9 !important;
  font-weight: bold  !important;
  box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.5)  !important;
  transition: transform 0.2s ease-in-out  !important;
}

.bd .btn:hover {
  transform: scale(0.9)  !important;
}